import React from "react";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import DirectionsBikeRoundedIcon from "@material-ui/icons/DirectionsBikeRounded";
import DirectionsCarRoundedIcon from "@material-ui/icons/DirectionsCarRounded";
import LocalShippingRoundedIcon from "@material-ui/icons/LocalShippingRounded";

import Chip from "@material-ui/core/Chip";
import Button from "@material-ui/core/Button";

import { useParams, Link } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";

import GoogleMapReact from "google-map-react";
import DriverMarkers from "../components/core/DriverMarkers";
import DriversContext from "../components/context/drivers/DriversContext";
import Axios from "axios";
import priceFormatter from "../components/core/priceFormatter";
import Markers from "../components/core/Markers";
import NotificationsContext from "../components/context/notifications/NotificationsContext";

const useStyle = makeStyles((theme) => ({
  distance: {
    // textShadow: "2px 2px 5px #000000",
    color: theme.palette.text.primary,
    position: "relative",
    bottom: "60vh",
    zIndex: 100,
    padding: theme.spacing(2),
  },
}));

const DeliveryInfo = () => {
  const { markers } = React.useContext(DriversContext);
  const classes = useStyle();
  const { id } = useParams();
  const ModeIcon = ({ mode, ...props }) => {
    switch (mode) {
      case "Motorcycle":
        return <DirectionsBikeRoundedIcon {...props} />;
      case "Car":
        return <DirectionsCarRoundedIcon {...props} />;
      case "Minivan":
        return <LocalShippingRoundedIcon {...props} />;
      default:
        return <React.Fragment />;
    }
  };
  const [review, setReview] = React.useState(0);
  const [loading, setLoading] = React.useState(false);
  const [delivery, setDelivery] = React.useState({
    client: null,
    dateCreated: null,
    distance: null,
    from: null,
    mode: null,
    price: null,
    status: null,
    to: null,
    track: null,
    driver: null,
    driverComment: [],
  });

  const {
    client,
    dateCreated,
    distance,
    from,
    mode,
    price,
    status,
    to,
    track,
    driver,
    driverComment,
  } = delivery;

  console.log("Delivery..", delivery);

  React.useEffect(() => {
    _getInfo();
  }, []);

  const _getInfo = async () => {
    setLoading(true);
    try {
      const res = await Axios.get(`/api/delivery/${id}`);
      setDelivery(res.data);
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  const assignToDriver = async (driver) => {
    try {
      console.log("Driver...", driver);
      await Axios.post("/api/admin/delivery/assign", {
        id,
        driverID: driver._id,
        reassign: delivery.driver,
      });
      setDelivery({ ...delivery, status: "Processing", driver });
    } catch (err) {
      console.log(err);
    }
  };

  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Delivery Info
        </Typography>
      </Box>
      <Box display="flex" flexDirection="row">
        <Typography variant="subtitle1" color="initial">
          {"ID: " + id}
        </Typography>
        <Box width={100} />
        <Typography variant="subtitle1" color="initial">
          Mode:
        </Typography>
        <Box width={10} />
        {mode && <ModeIcon color="primary" mode={mode} />}
        <Box width={100} />
        <Typography variant="subtitle1" color="initial">
          {price && "Price:  " + priceFormatter(price)}
        </Typography>
        <Box width={100} />
        {status && (
          <>
            <Typography variant="subtitle1" color="initial">
              Status
            </Typography>
            <Box width={10} />
            <Chip label={status} color="secondary" />
          </>
        )}
      </Box>
      <Box height="60vh" my={3}>
        <GoogleMapReact
          bootstrapURLKeys={{
            key: "AIzaSyAVa67y3wWEBxeAR5Q0p4PZtVknjarOlhQ",
          }}
          defaultCenter={{ lat: 4.8156, lng: 7.0498 }}
          defaultZoom={15}
        >
          {["Pending", "Processing"].includes(status) &&
            markers.map((marker, index) => (
              <DriverMarkers
                key={index}
                lat={marker.latitude}
                lng={marker.longitude}
                src={marker.photoUrl}
                name={marker.name}
                action={driver ? "Re-assign" : "Assign"}
                id={marker._id}
                animate
                onClick={() => {
                  console.log(marker);
                  assignToDriver(marker);
                }}
              />
            ))}
          {to && to.address && (
            <Markers
              lat={to.latitude}
              lng={to.longitude}
              color="secondary.main"
              address={to.address}
              id="to"
              animate
            />
          )}
          {from && from.address && (
            <Markers
              lat={from.latitude}
              lng={from.longitude}
              color="primary.main"
              address={from.address}
              id="from"
              animate
            />
          )}
        </GoogleMapReact>
        <Box>
          <Typography
            variant="h4"
            color="textSecondary"
            className={classes.distance}
          >
            {distance}
          </Typography>
        </Box>
      </Box>
      <Box display="flex" flexDirection="row" alignItems="center">
        <Button variant="contained" color="secondary">
          Track Order
        </Button>
        <Box display="flex" flexGrow={1} />
        {client && (
          <>
            <Typography variant="subtitle2"> Client: </Typography>
            <Box width={5} />
            <Typography
              variant="subtitle2"
              component={Link}
              to={`/dashboard/client/${client._id}`}
              color="textPrimary"
            >
              {client.name}
            </Typography>
          </>
        )}

        {driver && (
          <>
            <Box width={15} />
            <Typography variant="subtitle2"> Driver: </Typography>
            <Box width={5} />
            <Typography
              variant="subtitle2"
              component={Link}
              to={`/dashboard/driver/${driver._id}`}
              color="textPrimary"
            >
              {driver.name}
            </Typography>
          </>
        )}
      </Box>
    </React.Fragment>
  );
};

export default DeliveryInfo;
