import React, { useState } from "react";
import { EditingState } from "@devexpress/dx-react-grid";
import {
  Grid,
  Table,
  TableHeaderRow,
  TableEditColumn,
  TableInlineCellEditing,
} from "@devexpress/dx-react-grid-material-ui";
import { Box } from "@material-ui/core";

const getRowId = (row) => row.id;

const FocusableCell = ({ onClick, ...restProps }) => (
  <Table.Cell {...restProps} tabIndex={0} onFocus={onClick} />
);

const EditPrice = ({ columns, rowsStart, columnExtensions }) => {
  const [rows, setRows] = useState([]);
  const [editingCells, setEditingCells] = useState([]);

  React.useEffect(() => {
    if (Array.isArray(rowsStart)) {
      setRows(
        rowsStart.map((row, index) => {
          return { ...row, id: index };
        })
      );
    } // eslint-disable-next-line
  }, []);

  const commitChanges = ({ added, changed, deleted }) => {
    let changedRows;
    if (added) {
      const startingAddedId =
        rows.length > 0
          ? Math.max(rows[rows.length - 1].id, rows[0].id) + 1
          : 0;
      changedRows = [
        ...added.map((row, index) => ({
          id: startingAddedId + index,
          ...row,
        })),
        ...rows,
      ];
      setEditingCells([
        { rowId: startingAddedId, columnName: columns[0].name },
      ]);
    }
    if (changed) {
      changedRows = rows.map((row) =>
        changed[row.id] ? { ...row, ...changed[row.id] } : row
      );
    }
    if (deleted) {
      const deletedSet = new Set(deleted);
      changedRows = rows.filter((row) => !deletedSet.has(row.id));
    }
    setRows(changedRows);
  };

  const addEmptyRow = () => commitChanges({ added: [{}] });

  return (
    <Box minHeight={500}>
      <Grid rows={rows} columns={columns} getRowId={getRowId}>
        <EditingState
          onCommitChanges={commitChanges}
          editingCells={editingCells}
          onEditingCellsChange={setEditingCells}
          addedRows={[]}
          onAddedRowsChange={addEmptyRow}
        />
        <Table
          cellComponent={FocusableCell}
          columnExtensions={columnExtensions}
        />
        <TableHeaderRow />
        <TableInlineCellEditing selectTextOnEditStart />
        <TableEditColumn showAddCommand showDeleteCommand />
      </Grid>
    </Box>
  );
};

export default EditPrice;
