import React from "react";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";

import Button from "@material-ui/core/Button";

import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";

import GoogleMapReact from "google-map-react";
import Grid from "@material-ui/core/Grid";
import Avatar from "@material-ui/core/Avatar";
import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import ListItemSecondaryAction from "@material-ui/core/ListItemSecondaryAction";
import VerifiedUserRoundedIcon from "@material-ui/icons/VerifiedUserRounded";

import clsx from "clsx";

import Charts from "../components/core/Charts";
import DriverMarkers from "../components/core/DriverMarkers";
import { useContext } from "react";
import DriversContext from "../components/context/drivers/DriversContext";
import { useParams } from "react-router-dom";
import Axios from "axios";
import { CircularProgress } from "@material-ui/core";
import { groupByDay, groupByMonth } from "../components/core/utils";

const useStyle = makeStyles((theme) => ({
  avatar: {
    marginRight: theme.spacing(1),
    [theme.breakpoints.down("md")]: {
      height: theme.spacing(10),
      width: theme.spacing(10),
    },
    [theme.breakpoints.up("md")]: {
      height: theme.spacing(7),
      width: theme.spacing(7),
    },
    [theme.breakpoints.up("lg")]: {
      height: theme.spacing(10),
      width: theme.spacing(10),
    },
  },
  card: {
    width: "100%",
  },
  primary: {
    backgroundColor: theme.palette.primary.main,
  },
  primaryText: {
    color: theme.palette.primary.contrastText,
  },
}));

const DriverInfo = () => {
  const classes = useStyle();
  const { id } = useParams();
  const { drivers } = useContext(DriversContext);
  const [driver, setDriver] = React.useState(null);

  const [chartData, setChartData] = React.useState([]);
  const [barData, setBarData] = React.useState([]);
  const [monthSales, setMonthSales] = React.useState([]);
  const [monthRev, setMonthRev] = React.useState([]);
  const [weekSales, setWeekSales] = React.useState([]);
  const [weekRev, setWeekRev] = React.useState([]);

  const [driverInfo, setDriverInfo] = React.useState({});
  const [loading, setLoading] = React.useState(true);

  const [sales, setSales] = React.useState({ delivered: 0, cancelled: 0 });

  const [week, setWeek] = React.useState([]);

  const [month, setMonth] = React.useState([]);

  const getDriver = async () => {
    setLoading(true);
    try {
      const res = await Axios.get(`/api/admin/driver/${id}`);
      setDriverInfo(res.data);
      if (res.data.performance.deliveries.weekly)
        setChartData(groupByDay(res.data.performance.deliveries.weekly));
      if (res.data.performance.deliveries.monthly)
        setBarData(groupByMonth(res.data.performance.deliveries.monthly));
    } catch (err) {
      console.log(err);
    }
    setLoading(false);
  };

  React.useEffect(() => {
    setWeekSales(chartData.map((data) => data[0]));
    setWeekRev(chartData.map((data) => data[1]));
    setMonthRev(barData.map((data) => data[1]));
    setMonthSales(barData.map((data) => data[0]));
  }, [chartData, barData]);

  React.useEffect(() => {
    getDriver();
    // eslint-disable-next-line
  }, []);

  React.useEffect(() => {
    setDriver(drivers[id]);
  }, [drivers, id]);

  if (loading)
    return (
      <Box
        alignItems="center"
        justifyContent="center"
        display="flex"
        height="100%"
      >
        <CircularProgress />
      </Box>
    );
  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Driver
        </Typography>
      </Box>
      <Grid container spacing={3}>
        <Grid item xs={6} md={3}>
          <Paper style={{ maxWidth: 300 }}>
            <Box p={2}>
              <Grid
                container
                spacing={2}
                justify="flex-start"
                alignItems="center"
              >
                <Grid item xs={4}>
                  <Avatar
                    className={classes.avatar}
                    src={driverInfo.photoUrl}
                  />
                </Grid>
                <Grid item xs={8} justify="flex-start">
                  <Typography variant="h6" color="secondary" noWrap>
                    {driverInfo.name}
                  </Typography>
                  <Typography variant="body2" color="initial" noWrap>
                    {`Staff ID: ${driverInfo.staffID}`}
                  </Typography>
                </Grid>
              </Grid>
            </Box>
            <Divider />
            <List>
              <ListItem>
                <ListItemText primary="Email" secondary={driverInfo.email} />
                <ListItemSecondaryAction>
                  <VerifiedUserRoundedIcon />
                </ListItemSecondaryAction>
              </ListItem>
              <ListItem>
                <ListItemText primary="Phone" secondary={driverInfo.phone} />
                <ListItemSecondaryAction>
                  <Button variant="text" color="secondary">
                    View Orders
                  </Button>
                </ListItemSecondaryAction>
              </ListItem>
            </List>
          </Paper>
          <Box mt={5}>
            <Typography variant="body2" color="initial" gutterBottom>
              Today
            </Typography>
            <Paper className={clsx([classes.card, classes.primary])}>
              <Box p={2}>
                <Typography
                  variant="h6"
                  color="textPrimary"
                  className={classes.primaryText}
                >
                  Sales
                </Typography>
                <Box height={30} />
                <Typography
                  variant="body1"
                  color="textSecondary"
                  className={classes.primaryText}
                >
                  {sales.delivered} Delivered
                </Typography>
                <Typography
                  variant="body1"
                  color="textSecondary"
                  className={classes.primaryText}
                >
                  {sales.cancelled} Cancelled
                </Typography>
              </Box>
            </Paper>
          </Box>
        </Grid>
        <Grid item xs={6} md={4}>
          <Typography variant="body2" color="initial" gutterBottom>
            This week
          </Typography>
          <Charts
            data={weekSales}
            argument="date"
            lineValues={["sales", "cancelled"]}
          />

          <Box mt={5}>
            <Typography variant="body2" color="initial" gutterBottom>
              Monthly
            </Typography>
            <Charts
              data={monthSales}
              argument="date"
              barValues={["sales", "cancelled"]}
            />
          </Box>
        </Grid>
        <Grid item xs={12} md={5}>
          <Box height={550} width={1}>
            <GoogleMapReact
              bootstrapURLKeys={{
                key: "AIzaSyAVa67y3wWEBxeAR5Q0p4PZtVknjarOlhQ",
              }}
              defaultCenter={{ lat: 4.8156, lng: 7.0498 }}
              center={{
                lat: driver ? driver.latitude : 4.8156,
                lng: driver ? driver.longitude : 7.0498,
              }}
              defaultZoom={15}
            >
              {driver && (
                <DriverMarkers
                  lat={driver.latitude}
                  lng={driver.longitude}
                  src={driver.photoUrl}
                  name={driver.name}
                  id={id}
                  animate
                />
              )}
            </GoogleMapReact>
          </Box>
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default DriverInfo;
