import React, { useContext } from "react";
import Box from "@material-ui/core/Box";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import EditRoundedIcon from "@material-ui/icons/EditRounded";

import FormControlLabel from "@material-ui/core/FormControlLabel";
import Switch from "@material-ui/core/Switch";

import { makeStyles } from "@material-ui/core/styles";
import Paper from "@material-ui/core/Paper";

import Grid from "@material-ui/core/Grid";
import DriversContext from "../components/context/drivers/DriversContext";
import SocketContext from "../components/context/socket/SocketContext";
import MyVirtualTable from "../components/core/VirtualTableTheme";
import Axios from "axios";
import {
  Modal,
  Dialog,
  Button,
  DialogActions,
  DialogContent,
} from "@material-ui/core";
import EditPrice from "../components/core/EditPrice";

const useStyle = makeStyles((theme) => ({
  distance: {
    flexGrow: 1,
  },
}));

const columns = [
  { name: "distance", title: "Distance (km)" },
  { name: "price", title: "Price (#)" },
];
const columnExtension = [
  { columnName: "distance", align: "left" },
  { columnName: "price", width: 120, align: "right" },
];

const Pricing = () => {
  const classes = useStyle();
  const { modes } = useContext(DriversContext);
  const { switchModes } = useContext(SocketContext);
  const [motorcyleList, setMotorcyleList] = React.useState([]);
  const [carList, setCarList] = React.useState([]);
  const [minivanList, setMinivanList] = React.useState([]);

  const getPricing = async () => {
    try {
      const res = await Axios.get("/api/admin/price_matrix");
      const { motorcycle, car, minivan } = res.data;
      console.log("data", motorcycle, car, minivan);
      motorcycle && setMotorcyleList(motorcycle.priceList);
      car && setCarList(car.priceList);
      minivan && setMinivanList(minivan.priceList);
      console.log(res.data);
    } catch (err) {
      console.log(err);
    }
  };

  React.useEffect(() => (async () => getPricing())(), []);

  const Display = ({ matrix }) => {
    const [open, setOpen] = React.useState(false);
    return (
      <React.Fragment>
        {console.log(matrix, "matrix")}
        <MyVirtualTable
          rows={matrix}
          columns={columns}
          columnExtensions={columnExtension}
          footer={
            <Box width={1} p={1} display="flex" justifyContent="flex-end">
              <IconButton onClick={() => setOpen(true)}>
                <EditRoundedIcon />
              </IconButton>
            </Box>
          }
        />
        <Dialog open={open} onClose={() => setOpen(false)}>
          <DialogContent>
            <EditPrice
              columnExtensions={columnExtension}
              rowsStart={matrix}
              columns={columns}
            />
          </DialogContent>
          <DialogActions>
            <Button color="primary">Submit</Button>
            <Button color="default" onClick={() => setOpen(false)}>
              Cancel
            </Button>
          </DialogActions>
        </Dialog>
      </React.Fragment>
    );
  };
  return (
    <React.Fragment>
      <Box display="flex" flexDirection="row" mb={3}>
        <Typography variant="h5" color="initial" gutterBottom>
          Pricing Matrix
        </Typography>
      </Box>
      <Grid container spacing={5}>
        <Grid item xs={12} md={4}>
          <FormControlLabel
            control={
              <Switch
                checked={modes.includes("Motorcycle")}
                onClick={() => switchModes("Motorcycle")}
              />
            }
            label="Motorcycle"
            labelPlacement="start"
          />
          <Display matrix={motorcyleList} />
        </Grid>
        <Grid item xs={12} md={4}>
          <FormControlLabel
            control={
              <Switch
                checked={modes.includes("Car")}
                onClick={() => switchModes("Car")}
              />
            }
            label="Car"
            labelPlacement="start"
          />
          <Display matrix={carList} />
        </Grid>
        <Grid item xs={12} md={4}>
          <FormControlLabel
            control={
              <Switch
                checked={modes.includes("Mini Van")}
                onClick={() => switchModes("Mini Van")}
              />
            }
            label="Minivan"
            labelPlacement="start"
          />
          <Display matrix={minivanList} />
        </Grid>
      </Grid>
    </React.Fragment>
  );
};

export default Pricing;
